body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.konteiner {
  display: flex;
  padding-left: 80px;
  justify-content: center;
  grid-gap: 50px;
  gap: 50px;
  margin-top: 100px;
  margin-left: 200px;
  margin-right: 200px;
  margin-bottom: 100px;
}

h1 {
  padding: 10px;
}

li {
  list-style-type: none;
}

.Card {
  display: flex;
  flex-wrap: wrap;
}

img {
  margin-top: 15px;
  width: 100%;
}
.BookList {
  background-color: rgb(185, 247, 247);
  border-radius: 14px;
  justify-content: center;
  align-items: center;
  padding: 30px;
  max-width: 430px;
}

.card-container {
  background-color: rgb(255, 255, 255);
  padding: 20px;
  min-width: 350px;
  max-width: 00px;
  max-height: 830px;
  border-radius: 14px;
  box-shadow: 0px 10px 40px hsl(180, 2%, 36%);
  display: inline-block;
  margin: 20px;
  margin-top: 30px;
}

img {
  width: 100;
  border-radius: 8px;
}

